<template>
	<div class="cropper-wrap">
	  <div class="img-container">
	    <img ref="image" id="image"  :src="imgsrc">
	  </div>
	  <div class="cropper-bottom">
	  	  <div class="range">
	  	  	<li>{{angle}}</li>
	  	  	<van-slider v-model="val" :step="5" active-color="#e6292c" :min="0" :max="100" @change="rotation" />
	  	  </div>
		  <div class="small"></div>
		  <div class="handler-area">
			  <button ref="cancel" @click="cancel"></button>
			  <button ref="crop" @click="clip"></button>
		  </div>
	  </div>
  </div>

</script>
  
  </div>
</template>

<script>
import 'cropperjs/dist/cropper.css';
import Cropper from 'cropperjs';
import { Slider } from 'vant';


export default {
  props:['imgsrc'],
  data (){
    return {
      cropper:"",
      image:'',
      val:50
    }
  },
  components:{
  	vanSlider:Slider
  },
  computed:{
  	angle(){
  		return -180+this.val*3.6;
  	}
  },
  methods: {
    clip(){
         var res= this.cropper.getCroppedCanvas({
          width:200,
          height:200
         });
         var zhi=res.toDataURL();
         this.$emit("confirm",zhi)
    },
    rotation(value){
      this.val=value;
      this.cropper.rotateTo(this.angle);
    },
    cancel(){
    	this.$emit("close")
    }
  },

  mounted: function() {
    this.image=this.$refs['image']
    this.cropper = new Cropper(this.image, {
      aspectRatio: 1,
      viewMode:3,
      dragMode:"move",
      // cropBoxResizable:false,
      // autoCropArea:0.3,
      // cropBoxMovable:false,
      preview:".small", 
      crop(event) {

      }
    });
    

  }
}
</script>
<style>
  .cropper-point,.cropper-line{
    background: #e6292c !important;
  }
  .cropper-view-box {
    outline: 1px solid #e6292c !important;
    outline-color: rgba(255, 0, 0, 0.75) !important;
  }
</style>
<style  scoped>
.small{
  overflow: hidden;
  float: left;
  width: 16vh !important;
  height: 16vh !important;
}
.range{
	height: 8vh;
}
.range li{
	line-height: 30px;
}
#image {
  max-width:100%; 
  max-height:70vh;/* This rule is very important, please do not ignore this! */
}
.img-container{
	position: relative;
	max-height: 70vh;
}

.cropper-wrap{
	position: fixed;
	left: 0px;
	top:0px;
	bottom: 0;
	right: 0;
	background: #000;
	z-index: 100;
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	justify-content: center;
}

.cropper-bottom{
	align-self: flex-end;
	height: 26vh;
	box-sizing: border-box;
	margin: 0px 20px;
	width: calc(100% - 40px);
}
.handler-area{
	overflow: hidden;
}
.handler-area button{
	font-size: 20px;
	background: transparent;
	color: #e8e8e8;
	height: 8vh;
	line-height: 8vh;
	text-align: center;
	display: block;
	box-sizing: border-box;
	width: 100%;
}
.handler-area button:nth-child(1){
	color: #aaa;
}



</style>



